@media screen and (max-width: 768px) {
    .height {
        height: 31rem;
    }
}

@media (min-width: 768px) {
    .movie-card {
        height: 31rem;
    }
}

@media (min-width: 992px) {
    .movie-card {
        height: 33rem;
        width: auto;
    }
}

@media (min-width: 1200px) {
    .movie-card {
        height: 31rem;
    }
}